$breakpoint-phone: 768px;
@media (max-width: $breakpoint-phone) {
    .skills {
        top: 60% !important;
        width: 90% !important;
    }
    .tagcloud {
        float: none !important;
        margin: 10% 20% !important;
    }
}

.tagcloud-wrap {
    // background: gray;
    float: right;
    width: 100%;
    height: 100%;
    margin-top: 5%;
    .tagcloud {
        color: #ff4500;
        font-family: 'Poppins', sans-serif;
        font-size: 20px;
        font-weight: 650;
        float: right;
        margin-right: 10%;
    }
    .tagcloud--item:hover {
        color: #00ff7f;
    }
}