$breakpoint-phone: 768px;
@media (max-width: $breakpoint-phone) {
    .nav-bar {
        width: 13% !important;
    }
}

.nav-bar {
    background: #181818;
    width: 4%;
    height: 100%;
    position: absolute;
    top: 0;
    z-index: 3;
    min-height: 500px;
    .logo {
        display: block;
        padding: 8px 0;
        img {
            display: block;
            margin: 8px auto;
            width: 24px;
            height: auto;
            &.sub-logo {
                width: 50px;
            }
        }
    }
    nav {
        display: block;
        text-align: center;
        position: absolute;
        height: 210px;
        top: 50%;
        margin-top: -120px;
        width: 100%;
        a {
            font-size: 22px;
            color: #4d4d4e;
            display: block;
            line-height: 51px;
            height: 51px;
            position: relative;
            text-decoration: none;
            i {
                transition: all 0.3s ease-out;
            }
            &:hover {
                color: #ffd700;
                svg {
                    opacity: 0;
                }
                &:after {
                    opacity: 1;
                }
            }
            &:after {
                content: '';
                font-size: 9px;
                letter-spacing: 2px;
                position: absolute;
                bottom: 0;
                display: block;
                width: 100%;
                text-align: center;
                opacity: 0;
                -webkit-transition: all 0.3s ease-out;
                transition: all 0.3s ease-out;
            }
            &:first-child {
                &:after {
                    content: 'HOME';
                }
            }
        }
        a.about-link {
            &:after {
                content: 'ABOUT';
            }
        }
        a.portfolio-link {
            &:after {
                content: 'Portfolio';
            }
        }
        a.contact-link {
            &:after {
                content: 'CONTACT';
                font-size: 7px;
            }
        }
        a.add-login-link {
            &:after {
                content: 'sign-in';
                font-size: 7px;
            }
        }
        a.skills-link {
            &:after {
                content: 'SKILLS';
            }
        }
        a.pradd-link {
            &:after {
                content: 'Add Port';
            }
        }
        a.active {
            svg {
                color: #ffd700;
            }
        }
    }
    ul {
        position: absolute;
        bottom: 20px;
        width: 100%;
        display: block;
        padding: 0;
        list-style: none;
        text-align: center;
        margin: 0;
        li {
            a {
                padding: 7px 0;
                display: block;
                font-size: 15px;
                line-height: 16px;
                color: #4d4d4e;
                &:hover {
                    color: #ffd700;
                }
            }
        }
    }
}

.logout-link {
    padding: 7px 0;
    display: block;
    font-size: 15px;
    line-height: 16px;
    color: #4d4d4e;
}

.logout {
    background-color: transparent;
    border: none;
    font-size: 22px;
    &:hover {
        color: #ffd700 !important;
        svg {
            opacity: 0;
        }
        &:after {
            opacity: 1 !important;
        }
    }
    &:after {
        content: 'Logout';
        font-size: 9px;
        letter-spacing: 2px;
        position: absolute;
        top: 315px;
        left: 0px;
        bottom: 0;
        display: block;
        width: 100%;
        opacity: 0;
        text-align: center;
        -webkit-transition: all 0.3s ease-out;
        transition: all 0.3s ease-out;
    }
}

.random {
    color: #b9b9b9;
}