$breakpoint-phone: 768px;
@media (max-width: $breakpoint-phone) {
    .card_btn button {
        font-size: 19px !important;
    }
    .Cont_style {
        right: 58px !important;
    }
    .Card_style {
        width: 300px !important;
    }
    .text-zonep {
        left: 5% !important;
    }
}

.text-zonep {
    position: absolute;
    left: 150px;
    top: 50%;
    transform: translateY(-50%);
    width: 35%;
    vertical-align: middle;
    display: table-cell;
    max-height: 90%;
    h1 {
        font-size: 53px;
        font-family: 'Coolvetica';
        color: #00ff7f;
        font-weight: 400;
        margin-top: 0;
        position: relative;
        margin-bottom: 40px;
        left: -10px;
    }
    p {
        font-size: 17px;
        color: #fff;
        font-family: sans-serif;
        font-weight: 300;
        max-width: fit-content;
        animation: pulse 1s;
        &:nth-of-type(1) {
            animation-delay: 1.1s;
        }
        &:nth-of-type(2) {
            animation-delay: 1.2s;
        }
        &:nth-of-type(3) {
            animation-delay: 1.3s;
        }
    }
}

.text-animate-hover {
    &:hover {
        color: #ff4500;
    }
}

.port {
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 80%;
    height: 80vh;
    border: 1px solid #ccc;
    background: #fff;
    overflow: auto;
    border-radius: 4px;
    outline: none;
    padding: 20px;
}

.Card_style {
    height: 350px;
    width: 900px;
    text-align: center;
    background: #52C0F5;
    color: #FFF;
    font-family: sans-serif;
    font-size: 12px;
    text-transform: uppercase;
    border: #00ff7f;
    border-radius: 30px;
    overflow: hidden;
    box-sizing: border-box;
    margin: 0px;
}

.Cont_style {
    position: absolute;
    right: 0px;
    height: 100vh;
    width: 60%;
    display: flex;
    flex: 1;
    justify-content: center;
    align-items: middle;
}

.card-info {
    width: 100%;
    height: 100%;
}

.card-info img {
    height: 100%;
    width: 100%;
}

.card_btn {
    position: absolute;
    top: 40%;
    left: 35%;
    text-align: center;
}

.card_btn button:hover {
    background-color: #00ff7f !important;
    color: black;
    border: none !important;
}

.card_btn button {
    color: #00ff7f;
    font-size: 43px;
    background-color: none !important;
    font-weight: 400;
    letter-spacing: 4px;
    font-family: sans-serif;
    text-decoration: none;
    padding: 10px 18px;
    border: 1px solid #00ff7f;
    float: left;
    animation: fadeInAnimation 1s 1.8s backwards;
    white-space: nowrap;
    border-radius: 0.5rem;
}

@media (min-width: 576px) {
    .modal-dialog {
        min-width: 95% !important;
        float: right;
        margin: auto !important;
    }
    .modal-body {
        height: 90vh !important;
    }
}