$breakpoint-phone: 768px;
@media (max-width: $breakpoint-phone) {
    .text-zone {
        width: 90% !important;
        left: 6%!important;
        ;
        h1 {
            font-size: 33px !important;
        }
        img {
            width: 32px !important;
        }
    }
    h2 {
        font-size: 8px !important;
    }
}

.home-page {
    .text-zone {
        position: absolute;
        left: 10%;
        top: 50%;
        transform: translateY(-50%);
        width: 40%;
        max-height: 90%;
        h1 {
            color: #fff;
            font-size: 56px;
            line-height: 63px;
            margin: 0;
            font-family: 'Coolvetica';
            font-weight: 400;
        }
        .imgA {
            margin-left: 20px;
            opacity: 0;
            width: 52px;
            height: 55px;
            animation: rotateIn 1s linear both;
            animation-delay: 1.4s;
        }
        .imgI {
            opacity: 0;
            width: 45px;
            height: 55px;
            animation: rotateIn 1s linear both;
            animation-delay: 1.4s;
        }
        .imgM {
            opacity: 0;
            width: 45px;
            height: 55px;
            margin-right: 8px;
            animation: rotateIn 1s linear both;
            animation-delay: 1.4s;
        }
    }
    h2 {
        color: #8d8d8d;
        margin-top: 20px;
        font-weight: 400;
        font-size: 11px;
        font-family: sans-serif;
        letter-spacing: 3px;
        animation: fadeIn 1s 1.8s backwards;
    }
    .flat-button {
        color: #00ff7f;
        font-size: 13px;
        font-weight: 400;
        letter-spacing: 4px;
        font-family: sans-serif;
        text-decoration: none;
        padding: 10px 18px;
        border: 1px solid #00ff7f;
        margin-top: 25px;
        float: left;
        animation: fadeInAnimation 1s 1.8s backwards;
        white-space: nowrap;
        border-radius: 0.5rem;
        &:hover {
            background: #00ff7f;
            color: rgb(0, 0, 0);
        }
    }
}