$breakpoint-phone: 768px;
@media (max-width: $breakpoint-phone) {
    .about {
        top: 62% !important;
        width: 90%!important;
    }
    .about p {
        margin: 2px auto !important;
    }
    .stage-cube-cont {
        width: 100% !important;
    }
    .cubespinner {
        div {
            width: 150px !important;
            height: 150px !important;
        }
        .face1 {
            transform: translateZ(75px) !important;
            color: #dd0031;
            -webkit-transform: translateZ(75px) !important;
            -moz-transform: translateZ(75px) !important;
            -ms-transform: translateZ(75px) !important;
            -o-transform: translateZ(75px) !important;
        }
        .face2 {
            transform: rotateY(90deg) translateZ(75px) !important;
            color: #f06529;
            -webkit-transform: rotateY(90deg) translateZ(75px) !important;
            -moz-transform: rotateY(90deg) translateZ(75px) !important;
            -ms-transform: rotateY(90deg) translateZ(75px) !important;
            -o-transform: rotateY(90deg) translateZ(75px) !important;
        }
        .face3 {
            transform: rotateY(90deg) rotateX(90deg) translateZ(75px) !important;
            color: #28a4d9;
            -webkit-transform: rotateY(90deg) rotateX(90deg) translateZ(75px) !important;
            -moz-transform: rotateY(90deg) rotateX(90deg) translateZ(75px) !important;
            -ms-transform: rotateY(90deg) rotateX(90deg) translateZ(75px) !important;
            -o-transform: rotateY(90deg) rotateX(90deg) translateZ(75px) !important;
        }
        .face4 {
            transform: rotateY(180deg) rotateZ(90deg) translateZ(75px) !important;
            color: #5ed4f4;
            -webkit-transform: rotateY(180deg) rotateZ(90deg) translateZ(75px) !important;
            -moz-transform: rotateY(180deg) rotateZ(90deg) translateZ(75px) !important;
            -ms-transform: rotateY(180deg) rotateZ(90deg) translateZ(75px) !important;
            -o-transform: rotateY(180deg) rotateZ(90deg) translateZ(75px) !important;
        }
        .face5 {
            transform: rotateY(-90deg) rotateZ(90deg) translateZ(75px) !important;
            color: #efd81d;
            -webkit-transform: rotateY(-90deg) rotateZ(90deg) translateZ(75px) !important;
            -moz-transform: rotateY(-90deg) rotateZ(90deg) translateZ(75px) !important;
            -ms-transform: rotateY(-90deg) rotateZ(90deg) translateZ(75px) !important;
            -o-transform: rotateY(-90deg) rotateZ(90deg) translateZ(75px) !important;
        }
        .face6 {
            transform: rotateX(-90deg) translateZ(75px) !important;
            color: #ec4d28;
            -webkit-transform: rotateX(-90deg) translateZ(75px) !important;
            -moz-transform: rotateX(-90deg) translateZ(75px) !important;
            -ms-transform: rotateX(-90deg) translateZ(75px) !important;
            -o-transform: rotateX(-90deg) translateZ(75px) !important;
        }
    }
}

.stage-cube-cont {
    width: 50%;
    height: 100%;
    top: 0;
    padding-top: 18%;
    margin-left: 0;
    position: absolute;
    right: 0;
    overflow: hidden;
}

.cubespinner {
    animation-name: spincube;
    animation-timing-function: ease-in-out;
    animation-iteration-count: infinite;
    animation-duration: 12s;
    transform-style: preserve-3d;
    transform-origin: 100px 100px 0;
    margin-left: calc(50% - 75px);
    div {
        position: absolute;
        width: 200px;
        height: 200px;
        border: 1px solid #ccc;
        background: rgba(255, 255, 255, 0.4);
        text-align: center;
        font-size: 100px;
        display: flex;
        justify-content: center;
        align-items: center;
        box-shadow: 0 0 20px 0px lightyellow;
    }
    .face1 {
        transform: translateZ(100px);
        color: #dd0031;
        -webkit-transform: translateZ(100px);
        -moz-transform: translateZ(100px);
        -ms-transform: translateZ(100px);
        -o-transform: translateZ(100px);
    }
    .face2 {
        transform: rotateY(90deg) translateZ(100px);
        color: #f06529;
        -webkit-transform: rotateY(90deg) translateZ(100px);
        -moz-transform: rotateY(90deg) translateZ(100px);
        -ms-transform: rotateY(90deg) translateZ(100px);
        -o-transform: rotateY(90deg) translateZ(100px);
    }
    .face3 {
        transform: rotateY(90deg) rotateX(90deg) translateZ(100px);
        color: #28a4d9;
        -webkit-transform: rotateY(90deg) rotateX(90deg) translateZ(100px);
        -moz-transform: rotateY(90deg) rotateX(90deg) translateZ(100px);
        -ms-transform: rotateY(90deg) rotateX(90deg) translateZ(100px);
        -o-transform: rotateY(90deg) rotateX(90deg) translateZ(100px);
    }
    .face4 {
        transform: rotateY(180deg) rotateZ(90deg) translateZ(100px);
        color: #5ed4f4;
        -webkit-transform: rotateY(180deg) rotateZ(90deg) translateZ(100px);
        -moz-transform: rotateY(180deg) rotateZ(90deg) translateZ(100px);
        -ms-transform: rotateY(180deg) rotateZ(90deg) translateZ(100px);
        -o-transform: rotateY(180deg) rotateZ(90deg) translateZ(100px);
    }
    .face5 {
        transform: rotateY(-90deg) rotateZ(90deg) translateZ(100px);
        color: #efd81d;
        -webkit-transform: rotateY(-90deg) rotateZ(90deg) translateZ(100px);
        -moz-transform: rotateY(-90deg) rotateZ(90deg) translateZ(100px);
        -ms-transform: rotateY(-90deg) rotateZ(90deg) translateZ(100px);
        -o-transform: rotateY(-90deg) rotateZ(90deg) translateZ(100px);
    }
    .face6 {
        transform: rotateX(-90deg) translateZ(100px);
        color: #ec4d28;
        -webkit-transform: rotateX(-90deg) translateZ(100px);
        -moz-transform: rotateX(-90deg) translateZ(100px);
        -ms-transform: rotateX(-90deg) translateZ(100px);
        -o-transform: rotateX(-90deg) translateZ(100px);
    }
}

@keyframes spincube {
    from,
    to {
        transform: rotateX(0deg) rotateY(0deg) rotateZ(0deg);
    }
    16% {
        transform: rotateY(-90deg);
    }
    33% {
        transform: rotateY(-90deg) rotateZ(90deg);
    }
    50% {
        transform: rotateY(-180deg) rotateZ(90deg);
    }
    66% {
        transform: rotateY(-270deg) rotateX(90deg);
    }
    83% {
        transform: rotateX(90deg);
    }
}