$breakpoint-phone: 768px;
@media (max-width: $breakpoint-phone) {
    .addport {
        left: 5% !important;
        width: 100% !important;
    }
    .contact-form ul li.half {
        width: 100% !important;
    }
}

.pass {
    width: 100%;
    border: 0;
    background: #353535;
    height: 50px;
    font-size: 16px;
    color: #fff;
    padding: 0 20px;
    box-sizing: border-box;
}

.contact-form ul {
    margin: 0px !important;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.contact-form ul li.half {
    width: 49%;
    margin-left: 0% !important;
    float: left;
    clear: none;
}

.addport {
    position: absolute;
    left: 35%;
    top: 50%;
    transform: translateY(-50%);
    width: 35%;
    vertical-align: middle;
    display: table-cell;
    max-height: 90%;
    h1 {
        font-size: 53px;
        font-family: 'Coolvetica';
        color: #00ff7f;
        font-weight: 400;
        margin-top: 0;
        position: relative;
        margin-bottom: 40px;
        left: -10px;
    }
    p {
        font-size: 17px;
        color: #fff;
        font-family: sans-serif;
        font-weight: 300;
        max-width: fit-content;
        animation: pulse 1s;
        &:nth-of-type(1) {
            animation-delay: 1.1s;
        }
        &:nth-of-type(2) {
            animation-delay: 1.2s;
        }
        &:nth-of-type(3) {
            animation-delay: 1.3s;
        }
    }
}

.text-animate-hover {
    &:hover {
        color: #ff4500;
    }
}