$breakpoint-phone: 768px;
@media (max-width: $breakpoint-phone) {
    .map-wrap {
        background: rgba(8, 253, 216, 0.1);
        float: right;
        width: 101% !important;
        height: 26% !important;
        margin-top: 191% !important;
        /* margin-bottom: 0px; */
    }
    .contact-form {
        width: 90% !important;
    }
    .contactme {
        top: 30% !important;
        display: flex !important;
        flex-direction: column !important;
        align-items: center !important;
        h1 {
            font-size: 27px !important;
        }
    }
    .info-map {
        bottom: 226px !important;
        width: 100% !important;
        top: auto !important;
        padding: 5px 20px !important;
        right: 0px !important;
    }
    .contact-form .flat-button {
        padding: 8px 70px !important;
        font-size: 20px !important;
    }
    .contact-form ul li {
        display: flex !important;
        justify-content: center !important;
    }
}

.contact-form {
    width: 100%;
    margin-top: 20px;
    ul {
        padding: 0;
        margin: 0;
        li {
            padding: 0;
            margin: 0;
            border-radius: 0.5rem;
            list-style: none;
            margin-bottom: 10px;
            opacity: 0;
            overflow: hidden;
            display: block;
            clear: both;
            position: relative;
            animation: fadeInUp 2s 2s;
            animation-fill-mode: forwards;
        }
        li.half {
            width: 49%;
            margin-left: 2%;
            float: left;
            clear: none;
            &:first-child {
                margin-left: 0;
            }
        }
    }
    input[type='text'],
    input[type='email'] {
        width: 100%;
        border: 0;
        background: #353535;
        height: 50px;
        font-size: 16px;
        color: #fff;
        padding: 0 20px;
        box-sizing: border-box;
    }
    textarea {
        width: 100%;
        border: 0;
        background: #353535;
        height: 50px;
        font-size: 16px;
        color: #fff;
        padding: 20px;
        box-sizing: border-box;
        min-height: 150px;
    }
    .flat-button {
        color: #ff4500;
        font-size: 11px;
        letter-spacing: 3px;
        text-decoration: none;
        padding: 8px 10px;
        border: 1px solid #ff4500;
        float: left;
        border-radius: 4px;
        background: 0 0;
        text-transform: uppercase;
        float: right;
        text-align: center;
        margin-right: 10px;
        &:hover {
            cursor: pointer;
            background-color: #ff4500;
            color: #000;
        }
    }
}

.map-wrap {
    background: rgba(8, 253, 216, 0.1);
    float: right;
    width: 53%;
    height: 115%;
    margin-top: -3%;
}

.leaflet-container {
    position: relative;
    width: 100%;
    height: 100%;
    opacity: 0;
    animation: backInRight 1s 1.2s;
    animation-fill-mode: forwards;
}

.info-map {
    position: absolute;
    background: #000;
    top: 5px;
    right: 36%;
    z-index: 1;
    width: 267px;
    padding: 20px;
    color: #fff;
    font-family: 'Helvetica';
    font-size: 17px;
    font-weight: 300;
    opacity: 0;
    border-radius: 0.5rem;
    animation: fadeIn 1s 1.5s;
    animation-fill-mode: forwards;
    span {
        font-size: 16px;
        span {
            color: #ffd700;
        }
    }
}