$breakpoint-phone: 768px;
@media (max-width: $breakpoint-phone) {
    .addport2 {
        left: 4% !important;
        width: 100% !important;
    }
    .contact-form ul li {
        width: 100% !important;
    }
    .text-zone2 h1 {
        left: -15px !important;
    }
}

.abbtext {
    width: 100%;
}

.contact-form ul .txarea {
    width: 50%;
}

.add-form {
    width: 90% !important;
}

.text-zone2 textarea {
    width: 100%;
}

.text-zone2 {
    position: absolute;
    //left: 10%;
    top: 50%;
    left: 10%;
    transform: translateY(-50%);
    width: 100%;
    vertical-align: middle;
    display: table-cell;
    max-height: 90%;
    h1 {
        font-size: 53px;
        font-family: 'Coolvetica';
        color: #00ff7f;
        font-weight: 400;
        margin-top: 0;
        text-align: center;
        position: relative;
        margin-bottom: 40px;
        left: -76px;
    }
    p {
        font-size: 17px;
        color: #fff;
        font-family: sans-serif;
        font-weight: 300;
        max-width: fit-content;
        animation: pulse 1s;
        &:nth-of-type(1) {
            animation-delay: 1.1s;
        }
        &:nth-of-type(2) {
            animation-delay: 1.2s;
        }
        &:nth-of-type(3) {
            animation-delay: 1.3s;
        }
    }
}

.text-animate-hover {
    &:hover {
        color: #ff4500;
    }
}

.add-form ul li.half {
    width: 99%;
    margin-left: 0% !important;
    float: left;
    clear: none;
}

