$breakpoint-phone: 768px;
@media (max-width: $breakpoint-phone) {
    .page {
        width: 87% !important;
        float: right;
    }
    .container {
        height: 100vh!important;
        position: relative !important;
    }
    .tags {
        left: 60px !important;
    }
    .top-tags {
        top: 11px !important;
    }
}

.page {
    width: 100%;
    height: 100%;
    user-select: none;
}

.top-tags {
    bottom: auto;
    top: 35px;
}

.tags {
    // color: #ffd700;
    // color: #ff4500; // red
    color: #00ff7f; // green
    opacity: 0.9;
    position: absolute;
    bottom: 0;
    left: 120px;
    font-size: 18px;
    font-family: 'La Belle Aurore', cursive;
}

.bottom-tag-html {
    margin-left: -20px;
}

.container {
    width: 100%;
    will-change: contents;
    height: 100%;
    min-height: 566px;
    position: absolute;
    opacity: 0;
    top: 5%;
    margin: 0 auto;
    z-index: 1;
    transform-style: preserve-3d;
    animation: fadeIn 1s forwards;
    animation-delay: 1s;
}
@media (min-width: 428px){

    .container {
        max-width: 100% !important;
    }
}
.container.contact-page,
.container.about-page,
.container.portfel-page,
.container.skills-page {
    .text-zone {
        position: absolute;
        left: 10%;
        top: 50%;
        transform: translateY(-50%);
        width: 35%;
        vertical-align: middle;
        display: table-cell;
        max-height: 90%;
        h1 {
            font-size: 53px;
            font-family: 'Coolvetica';
            color: #00ff7f;
            font-weight: 400;
            margin-top: 0;
            position: relative;
            margin-bottom: 40px;
            left: -10px;
        }
        p {
            font-size: 17px;
            color: #fff;
            font-family: sans-serif;
            font-weight: 300;
            max-width: fit-content;
            animation: pulse 1s;
            &:nth-of-type(1) {
                animation-delay: 1.1s;
            }
            &:nth-of-type(2) {
                animation-delay: 1.2s;
            }
            &:nth-of-type(3) {
                animation-delay: 1.3s;
            }
        }
    }
    .text-animate-hover {
        &:hover {
            color: #ff4500;
        }
    }
}

// n is number of stars required
@function multiple-box-shadow($n) {
    $value: '#{random(2000)}px #{random(2000)}px ';
    @for $i from 2 through $n {
        $value: '#{$value} , #{random(2000)}px #{random(2000)}px ';
    }
    @return unquote($value);
}

$shadows-small: multiple-box-shadow(1400);
$shadows-medium: multiple-box-shadow(400);
$shadows-big: multiple-box-shadow(200);
html {
    height: 100%;
    // background: radial-gradient(ellipse at bottom, #1B2735 0%, #090A0F 100%);
    overflow: hidden;
}

#stars {
    width: 1px;
    height: 1px;
    background: transparent;
    box-shadow: $shadows-small;
    animation: animStar 50s linear infinite;
    &:after {
        content: '';
        position: absolute;
        top: 2000px;
        // width: 20px;
        // height: 20px;
        font-size: 20px;
        //color: #00ff7f;
        //background: black;
        box-shadow: $shadows-small;
    }
}

#stars2 {
    width: 2px;
    height: 2px;
    background: transparent;
    box-shadow: $shadows-medium;
    animation: animStar 100s linear infinite;
    &:after {
        content: ' ';
        position: absolute;
        top: 2000px;
        width: 2px;
        height: 2px;
        background: transparent;
        box-shadow: $shadows-medium;
    }
}

#stars3 {
    width: 3px;
    height: 3px;
    background: transparent;
    box-shadow: $shadows-big;
    animation: animStar 150s linear infinite;
    &:after {
        content: "A";
        position: absolute;
        top: 2000px;
        width: 100%;
        height: 100%;
        font-size: 20px;
        color: white;
        background: transparent;
        box-shadow: $shadows-big;
    }
}

@keyframes animStar {
    from {
        transform: translateY(-2000px);
        -webkit-transform: translateY(-2000px);
        -moz-transform: translateY(-2000px);
        -ms-transform: translateY(-2000px);
        -o-transform: translateY(-2000px);
    }
    to {
        transform: translateY(0px);
        -webkit-transform: translateY(0px);
        -moz-transform: translateY(0px);
        -ms-transform: translateY(0px);
        -o-transform: translateY(0px);
    }
}